<template>
	<div>
		<slot></slot>
	</div>
</template>

<script>
export default {
	name: 'de-form',
	provide() {
		return {
			Form: this
		}
	},
	props: {
		modal: {
			type: Object,
			required: true
		},
		rules: {
			type: Object
		}
	},
	created() {
		// mounted
		this.fileds = []
		this.resetFiledsArray = []
		this.$on('add.Fileds', item => {
			this.fileds.push(item)
		})
		// 
		this.$on('reset.fileds', item => {
			this.resetFiledsArray.push(item)
		})
	},
	methods: {
		// 
		validate(cb) {
			// 
			// Promise
			const tasks = this.fileds.map(item => item.validate())
			Promise.all(tasks).then(() => {
				cb(true)
			})
			.catch(() => {
				cb(false)
			})
		},
		//
		resetFileds() {
			this.resetFiledsArray.map(item => {
				item.resetFileds()
			})
		}
	}
}
</script>

<style>
@import './style.css'
</style>
