<template>
	<div class="inline-container" :class="appendClass">
		<template v-if="type==='text' || type === 'password'">
			<div class="de-input-prepend" v-if="$slots.prepend">
				<slot name="prepend"></slot>
			</div>
			<input
				class="de-input" 
				autoComplete="new-password"
				:type="type"
				v-bind="$attrs"
				v-model="inputValue"
				:disabled="disabled"
				v-on="myListeners"
				:class="styleClass"
				:maxlength="maxlength"
				@blur="validateData"
			>
			<div class="de-input-append" v-if="$slots.append">
				<slot name="append"></slot>
			</div>
			<de-icon v-if="showClearable" @click.native="clearInputValue" class="btn-close-icon" icon="close"></de-icon>
		</template>
		<template v-else>
			<textarea
				class="de-textarea" 
				v-bind="$attrs"
				v-model="inputValue"
				v-on="myListeners"
				@blur="validateData"
			>
			</textarea>
		</template>
	</div>
</template>

<script>
import DeIcon from '../icon/index.vue'
export default {
	name: 'de-input',
	inject: {
		Form: {
			default: null
		},
		FormItem: {
			default: null
		}
	},
	props: {
		value: {
			type: [String, Number],
			default: ''
		},
		// inputtexttextarea
		type: {
			type: String,
			default: 'text',
			validator: (value) => {
				return ['textarea', 'text', 'password'].includes(value)
			}
		},
		// 
		size: {
			type: String,
			default: '',
			validator: (value) => {
				return ['', 'medium', 'small'].includes(value)
			}
		},
		// 
		clearable: {
			type: Boolean,
			default: false
		},
		// 
		center: {
			type: Boolean,
			default: false
		},
		// 
		disabled: {
			type: Boolean,
			default: false
		},
		maxlength: {
			type: Number,
			default: 100
		}
	},
	components: {
		DeIcon
	},
	mounted() {
		if(this.FormItem) {
			this.FormItem.$on('reset', () => {
				this.inputValue = ''
			})
		}
	},
	methods: {
		clearInputValue() {
			this.inputValue = ''
		},
		// 
		validateData() {
			// 
			// formitem
			if(this.FormItem) {
				this.FormItem.$emit('validate')
			}
		}
	},
	computed: {
		inputValue: {
			set(value){
				this.$emit('input', value)
			},
			get() {
				return this.value
			}
		},
		myListeners() {
			return Object.assign(this.$listeners, {
				input: event => this.$emit('input', event.target.value)
			})
		},
		styleClass() {
			return {
				[`de-input-${this.size}`]: this.size,
				[`is-center`]: this.center,
				[`is-disabled`]: this.disabled
			}
		},
		// 
		showClearable() {
			return this.clearable && this.inputValue !== ''
		},
		// 
		appendClass() {
			return {
				'has-prepend': this.$slots.prepend,
				'has-append': this.$slots.append
			}
		}
	}
}
</script>

<style scoped>
@import './style.css';
</style>
