<template>
	<label class="de-radio">
		<span class="de-radio-outer" >
			<span class="de-radio-inner" :class="{ 'is-checked': isChecked }"></span>
			<input 
			  type="radio" 
			  class="de-radio-native" 
			  :value="label"
			  v-model="radioValue">
		</span>
		<span class="de-radio-label">{{ label }}</span>
	</label>
</template>

<script>
export default {
	name: 'de-radio',
	inject: {
		radioGroup: {
			default: ''
		}
	},
	props: {
		value: {
			type: [String, Number]
		},
		label: {
			type: [String, Number]
		}
	},
	computed: {
		// v-modelgetterssetter
		radioValue: {
			set (value) {
				// v-modelinput
				// :value="selectItem" @input="handleInput"
				this.$emit('input', value)
				this.isGroup ? this.radioGroup.$emit('input', value) : this.$emit('input', value)
			},
			get () {
				return this.isGroup ? this.radioGroup.value : this.value
			}
		},
		isChecked () {
			// label
			return this.radioValue === this.label
		},
		// radioGroup
		isGroup () {
			return this.radioGroup !== ''
		}
	}
}
</script>

<style scoped>
@import './index.css';
</style>